import App from './App';

require('../screens/canvas/stylesLogin.css');

export default class Login {
  constructor() {
    this.login = 'admin';
    this.password = 'admin';
    this.app = new App();
  }

  start() {
    const loginEntry = this.login;
    const passwEntry = this.password;
    const { app } = this;
    function authorization(e) {
      e.preventDefault();
      const login = document.querySelector('#login-email').value;
      const password = document.querySelector('#login-password').value;
      if (login === loginEntry && password === passwEntry) {
        document.querySelector('#btn-submit').removeEventListener('click', authorization);
        window.location.href = 'main-page.html';
        app.start();
      }
    }
    console.log('this.login', this.login);
    console.log('this.password', this.password);
    document.querySelector('#btn-submit').addEventListener('click', authorization);
  }
}
